.decor-card {
  margin-bottom: 24px;
  padding: 16px 0 16px 0;

  &__container {
    padding: 0 16px 0 16px;
  }

  &__top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 46px;
  }

  &__middle {
    display: flex;
    align-items: flex-end;
    margin-bottom: 32px;
  }

  &__name {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    padding-bottom: 8px;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--extra-dark-color);
  }

  &__product-vendor-code {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--dark-color);
  }

  &__fun {
    display: flex;
    height: 56px;

    &-item {
      &:not(:last-child) {
        margin-right: 4px;
      }

      &:first-child {
        margin-right: 6px;
      }
    }
  }

  &__type-wrap {
    max-width: 56px;
    flex-shrink: 0;
  }

  &__type {
    position: relative;
    height: 56px;
    margin-bottom: 4px;

    .ramka-svg {
      width: 56px;
      height: 56px;
      fill: #dfe3eb;
    }

    .zamkoviy-svg {
      position: absolute;
      width: 30px;
      height: 30px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      fill: var(--main-svg-color);
    }

    &-name {
      font-weight: 600;
      font-size: 10px;
      line-height: 12px;
      color: var(--extra-dark-color);
    }

    &--collection {
      background-color: var(--main-bg);
    }
  }

  &__drop {
    @include flex-all-center;

    width: 56px;
    height: 56px;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    text-transform: uppercase;
    color: var(--extra-dark-color);
    background-color: var(--main-bg);
  }

  &__size {
    height: 56px;

    &-item {
      width: 40px;
      height: 26px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      font-weight: 600;
      font-size: 10px;
      line-height: 12px;
      color: var(--extra-dark-color);
      background-color: var(--main-bg);

      &:not(:last-child) {
        margin-bottom: 4px;
      }

      &--active {
        color: var(--light-color);
        background-color: var(--extra-bg);
      }
    }
  }

  &__visual {
    @include flex-all-center;

    svg {
      width: 40px;
      height: 40px;
      fill: #0d111a;
    }

    &-link {
      display: block;
      padding: 5px 20px 5px 5px;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      color: var(--extra-dark-color);
    }

    &-name {
      max-width: 98px;
      background-color: var(--main-bg);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--dark-color);
  }

  &__img {
    margin-right: 24px;
  }

  &__stock {
    max-width: 198px;
    margin-right: 76px;
  }

  &__text {
    margin: 0;
  }

  &__packing {
    max-width: 158px;
    margin-right: 98px;
  }

  &__prices {
    display: flex;
    flex-direction: column;
  }

  &__price {
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    color: var(--dark-color);
  }

  &__p-price {
    margin: 0;
    padding-bottom: 8px;
    font-weight: 600;
    font-size: 32px;
    line-height: 38px;
    color: var(--dark-color);
  }

  &__p-price-text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #4e5359;
  }
}

.all-decor {
  &__title {
    margin: 0;
    padding-bottom: 16px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: var(--extra-dark-color);
  }

  &__list {
    display: flex;
    line-height: 0;
  }

  &__item {
    position: relative;
    flex-shrink: 0;
    flex-wrap: wrap;
    border: 2px solid transparent;
    transition: 0.2s;

    &:hover {
      border: 2px solid var(--accent-color);
      transition: 0.2s;

      .all-decor__popup {
        opacity: 1;
        transform: translateY(0px);
        pointer-events: auto;
      }
    }

    &:not(:last-child) {
      margin-right: 24px;
    }
  }

  &__popup {
    position: absolute;
    bottom: 100%;
    left: 0;
    margin-bottom: 13px;
    width: 136px;
    padding: 8px;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
    opacity: 0;
    transform: translateY(13px);
    transition: all 0.2s ease-in-out;
    pointer-events: none;
    z-index: 100;
    background: #fff;

    &-img {
      background-image: url(https://cdn.vkvadrate.ru/lvt/Fine_Price/Forbo_Home_Expert/img3d/2028/2028_plank_1.jpg);
      width: 100%;
      padding-bottom: 100%;
      background-position: center;
      background-size: cover;
      margin-bottom: 8px;
    }

    &-name {
      font-size: 10px;
      line-height: 13px;
      letter-spacing: 0.02em;
      color: #4e5359;
    }
  }
}
