.decor-nav {
  &-wrap {
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    z-index: 100;

    &__left {
      margin-right: 16px;
      padding: 24px 16px;
      flex-grow: 1;
      border-radius: 8px;
      background-color: var(--light-color);
      box-shadow: 0px 2px 5px rgba(38, 51, 77, 0.03);
    }

    &__right {
      position: relative;
      max-width: 325px;
      display: flex;
      align-items: center;
      flex-grow: 1;
      padding: 16px 16px 16px 20px;
      border-radius: 8px;
      background-color: var(--light-color);
      box-shadow: 0px 2px 5px rgba(38, 51, 77, 0.03);
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    &::after {
      width: 100%;
      display: block;
      content: "";
      margin-top: 10px;
      height: 2px;
      color: var(--more-dark-color);
      background-color: transparent;
    }

    &--active {
      &::after {
        background-color: var(--accent-color);
      }
    }
  }

  &__link {
    padding: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: var(--more-dark-color);
  }

  &__popup {
    display: flex;
    align-items: center;
  }

  &__popup-menu {
    position: absolute;
    top: 0;
    left: 0;
  }
}
