.sidebar {
  width: 104px;
  min-height: 892px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px 24px 24px 24px;
  background-color: var(--light-color);
  overflow: hidden;
  transition: all 0.3s ease-in-out;

  &__bottom {
    display: flex;
    align-items: flex-end;
  }

  &__descr {
    margin: 0 0 0 16px;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    color: var(--copy-descr-color);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }

  &__link-app {
    svg {
      width: 8px;
      height: 54px;
      color: var(--extra-dark-color);
    }
  }

  &--collapse {
    .sidebar {
      width: 104px;
    }
    .sidebar:hover {
      width: 104px;
    }
    .sidebar-nav {
      width: 52px;
    }
  }
}

.sidebar .t-catalog__outer {
  width: 1238px !important;
}

.sidebar-nav {
  overflow: hidden;
  transition: all 0.3s ease-in-out;

  &__item:not(:last-child) {
    margin-bottom: 4px;
  }

  &__item:nth-child(5n):after {
    display: block;
    content: "";
    height: 1px;
    width: 100%;
    border-radius: 8px;
    background-color: var(--divider-color);
    margin-top: 16px;
  }

  &__item:nth-child(5n) {
    margin-bottom: 16px;
  }

  &__item:nth-child(6),
  &__item:nth-child(11) {
    margin-top: 16px;
  }

  &__link {
    width: 100%;
    display: flex;
    align-items: center;
    padding: 12px 16px;
    border-radius: 8px;
    color: var(--extra-dark-color);
    transition: background-color 0.1s !important;

    svg {
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      fill: var(--main-svg-color);
    }

    &:hover {
      color: var(--light-color);
      background-color: var(--main-bg);

      svg {
        fill: var(--accent-color);
      }

      .sidebar-nav__title {
        color: var(--accent-color);
      }
    }

    &--active {
      color: var(--light-color);
      background-color: var(--accent-color);

      svg {
        fill: var(--light-color);
      }

      &:hover {
        background-color: var(--accent-color);

        svg {
          fill: var(--light-color);
        }

        .sidebar-nav__title {
          color: var(--light-color);
        }
      }
    }
  }

  &__title {
    margin-left: 16px;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.02em;
  }
}

.sidebar-wrapper {
  width: 104px;
  position: relative;
  z-index: 200;
  transition: all 0.3s ease-in-out;

  &.hover .sidebar {
    width: 246px;
    transition: all 0.3s ease-in-out;
  }

  &.hover .sidebar__descr {
    opacity: 1;
    visibility: visible;
    transition: all 1.2s ease-in-out;
  }
}

.sidebar-wrapper.expanded {
  width: 246px;

  & .sidebar__descr {
    opacity: 1;
    visibility: visible;
    transition: all 1.2s ease-in-out;
  }
}

.sidebar.expanded {
  width: 246px;
}

.expand-svg {
  &.replaced-svg {
    display: none;
  }
}

.collapse-svg {
  display: none;
  &.replaced-svg {
    display: block;
  }
}
