.header {
  &__container {
    padding-top: 16px;
    padding-bottom: 16px;
    border-bottom: 2px solid #edeff2;
    background-color: var(--light-color);

    @include flex-all-sb;
  }
}

.menu {
  &__list {
    @include flex-all-sb;
  }

  &__item {
    position: relative;
    width: 48px;
    height: 48px;
    padding: 12px;
    border-radius: 8px;
    transition: all 0.3s ease-in-out;

    &:not(:first-child):not(:last-child):hover {
      background-color: var(--main-bg);

      svg {
        fill: var(--accent-color);
      }
    }

    &:first-child:hover {
      background-color: var(--main-bg);
    }

    &--user {
      border: 2px solid var(--user-border-color);
      padding: 0;
      flex-shrink: 0;
    }

    &--switch {
      padding: 12px 0;

      &:hover {
        background: none;
      }
    }

    &:not(:nth-last-child(-n + 2)) {
      margin-right: 16px;
    }

    &:nth-last-child(1) {
      margin-left: 24px;
    }

    svg {
      width: 24px;
      height: 24px;
      fill: var(--main-svg-color);
      transition: all 0.3s ease-in-out;
    }

    &--active {
      width: 100%;
      background-color: var(--main-bg);
    }

    &--active:nth-child(n + 2):nth-child(-n + 4)::before {
      position: absolute;
      top: -4px;
      left: 44px;
      content: "";
      display: block;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: var(--accent-color);
      transform: translateX(-24px);
    }
  }
}

.cart-info {
  display: flex;

  &__descr {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-left: 20px;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
  }

  &__count {
    font-weight: 600;
  }

  &__sum {
    font-weight: 600;
  }
}

.user {
  &__icon {
    height: 100%;

    @include flex-all-center;
  }

  &__image {
    border-radius: 8px;
  }
}

.switch-theme {
  position: relative;
  display: block;
  width: 48px;
  height: 24px;

  &__input {
    opacity: 0;
    width: 0;
    height: 0;

    &:checked + .switch-theme__slider {
      background-color: var(--accent-color);

      &:hover {
        background-color: #b30000;
      }
    }

    &:checked + .switch-theme__slider::before {
      transform: translateX(24px);
    }
  }

  &__slider {
    position: absolute;
    border-radius: 16px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--theme-checkbox-bg);
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
      background-color: #c7ced5;
    }

    &::before {
      position: absolute;
      content: "";
      border-radius: 50%;
      width: 20px;
      height: 20px;
      top: 2px;
      left: 2px;
      background-color: var(--light-color);
      box-shadow: 0px 2px 4px rgba(29, 31, 33, 0.2);
      transition: all 0.3s ease-in-out;
    }
  }
}

.top-search {
  &__wrapper {
    max-width: 100%;
    padding: 0;
    flex-shrink: 0;

    &--wider {
      width: 360px;
    }
  }

  &__container {
    display: flex;
    width: 48px;
    height: 48px;
    max-width: 100%;
    border: 2px solid transparent;
    border-radius: 8px;
    transition: color 0.5s ease-in-out;

    &--expanded {
      width: 100%;
      background-color: var(--main-bg);

      & .top-search__form {
        width: 100%;
        padding: 4px;
      }
    }
  }

  &__form {
    width: 0px;
    align-self: center;
  }

  &__input {
    all: unset;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--input-color);
    transition: all 0.3s ease-in-out;

    &::placeholder {
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: var(--input-placeholder-color);
    }
  }
}
