.decor {
  background-color: var(--main-bg);

  &__flex {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-left: 16px;
  }

  &__container {
    padding: 0 40px 0 0;
    display: flex;
  }

  &__title {
    margin: 0;
    padding-bottom: 24px;
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;
    color: var(--extra-dark-color);

    &--small {
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
    }

    &--mod {
      padding-bottom: 42px;
    }

    &--indent {
      margin-left: 16px;
    }
  }
}
