.catalog {
  background-color: #f7f8fa;

  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__container {
    padding-left: 0;
  }

  &__title {
    margin: 16px 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: var(--extra-dark-color);
  }

  &__flex {
    display: flex;
    flex-direction: column;
  }
}
