.dataTables_wrapper {
  align-self: flex-start !important;
  padding: 16px 0;
  width: 1200px;
  transition: all 0.3s ease-in-out;

  &.collapse {
    width: 1114px !important;
  }
}

.dataTables_length {
  display: none;
}

.dataTables_scrollHeadInner {
  padding-right: 0 !important;
}

.dataTables_scrollHeadInner {
  width: 100%;
}

.dataTables_paginate {
  width: 100%;
  display: flex;
  background-color: var(--light-color);
}

table.dataTable thead > tr > th:nth-child(1),
table.dataTable thead > tr > td:nth-child(1) {
  position: sticky;
  left: 0;
  padding-right: 26px;
}

table.dataTable thead > tr > th:nth-last-child(1),
table.dataTable tbody > tr > td:nth-last-child(1) {
  &::after {
    display: none !important;
  }

  &::before {
    display: none !important;
  }
}

.dataTables_scrollBody {
  background-color: var(--light-color);

  &::-webkit-scrollbar {
    height: 4px;
    width: 4px;
  }

  &::-webkit-scrollbar:vertical {
    height: 4px;
    width: 4px;
  }

  &::-webkit-scrollbar-track {
    background: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #c3cad9;
    border-radius: 8px;
  }
}

table.dataTable thead th,
table.dataTable thead td {
  padding-top: 16px;
  padding-bottom: 16px;
}

table.dataTable tbody th,
table.dataTable tbody td {
  padding: 0;
}

table.dataTable {
  margin: 0;
}

.paginate_button.current {
  border: 2px solid var(--accent-color) !important;
  background: var(--light-color) !important;
}

.paginate_button.current:hover {
  border: 2px solid transparent;
}

.dataTables_wrapper .dataTables_paginate {
  padding: 15px 16px;
  border-top: 1px solid var(--t-select-row-color) !important;
}

.dataTables_wrapper.no-footer .dataTables_scrollBody {
  border-bottom: none;
  background: transparent;
}

.dataTables_wrapper .dataTables_paginate .paginate_button {
  padding: 8.5px 12px;
  border: 2px solid transparent;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
}

.dataTables_wrapper .dataTables_paginate .paginate_button:active {
  background: var(--light-color) !important;
  border: 2px solid transparent;
  box-shadow: none;
  padding: 8.5px 12px;
}

.dataTables_wrapper .dataTables_paginate .paginate_button:hover {
  border: 2px solid var(--main-bg);
  background: var(--main-bg);
  color: #000 !important;
}

table.dataTable thead th,
table.dataTable thead td {
  border-bottom: 1px solid var(--light-gray-color);
}

.paginate_button:not(:last-child) {
  margin-right: 8px;
}

.dataTables_empty {
  font-size: 12px;
}

.dataTables_filter {
  display: none;
}

.specnew {
  width: 9.55% !important;
}

table.dataTable thead > tr > th.sorting,
table.dataTable thead > tr > th.sorting_asc {
  padding-right: 10px;
}
